.main {
  display:flex;
  --s: 300px;  /* size  */
  --m: 2px;    /* margin */
  --f: calc(1.732 * var(--s) + 4 * var(--m)  - 1px);
}

.container {
  font-size: 0; /*disable white space between inline block element */
  padding-left: "40px";
  padding-top: "40px";
  width: 100%;
}

.container div {
  width: var(--s);
  margin: var(--m);
  height: calc(var(--s)*1.1547); 
  display: inline-block;
  font-size:initial;
  clip-path: polygon(0% 25%, 0% 75%, 50% 100%, 100% 75%, 100% 25%, 50% 0%);
  -webkit-transition: background 300ms; /* For Safari 3.0 to 6.0 */
  transition: background 300ms; /* For modern browsers */
  background: rgba(26, 40, 49, 0.727);
  margin-bottom: calc(var(--m) - var(--s)*0.2885); 
  color: white;
}
.container div:nth-child(odd) {
  background:rgba(27, 62, 84, 0.566);
}

.container div:hover {
    clip-path: polygon(0% 25%, 0% 75%, 50% 100%, 100% 75%, 100% 25%, 50% 0%);
    background: rgba(15, 113, 174, 0.839);
    color: rgb(4, 0, 0);
    -webkit-transition: background 300ms; /* For Safari 3.0 to 6.0 */
    transition: background 300ms; /* For modern browsers */
    cursor: pointer;
  }

.container::before {
  content: "";
  width: calc(var(--s)/2 + var(--m));
  float: left;
  height: 120%;
  shape-outside: repeating-linear-gradient(     
                   #0000 0 calc(var(--f) - 3px),      
                   #000  0 var(--f));
}



